import React, { useState, useEffect } from "react";
import Axios from "axios";
import { isAutheticated } from "../auth";
import { Link, Navigate } from "react-router-dom";
import "../App.css";

const User = () => {
  const [value, setValue] = useState({
    success: "",
    error: "",
  });
  // destructuring token from localstorage
  const { token } = isAutheticated();

  // dataObject for api call
  const dataType = {
    headers: {
      authorization: `Bearer ${token}`,
    },
  };

  console.log(`dataType`, dataType);

  // // get api call
  const axiosGetCall = async () => {
    try {
      const res = await Axios.get(`api/user`, dataType);

      setValue({ ...value, error: "", success: res.data.dataDB.username });

      console.log(`res`, res);
    } catch (error) {
      console.log(error);
      setValue({ ...value, error: error.response.data.error, success: "" });
    }
  };

  useEffect(() => {
    axiosGetCall();
    // eslint-disable-next-line
  }, []);
  return (
    <>
      {value.error ? <>You are Not logged in: {value.error}</> : <></>}
      <br />
      {value.success ? (
        <>
          You are logged in: {value.success}{" "}
          <Link to="/logout" style={{ color: "black" }}>
            LOgout
          </Link>
        </>
      ) : (
        <></>
      )}
    </>
  );
};

export default User;
