import logo from "./logo.svg";

import { useState } from "react";
import { Navigate, Link } from "react-router-dom";
import { isAutheticated, authenticate } from "./auth";
import "./App.css";

import * as waxjs from "@waxio/waxjs/dist";
import { WaxAuthClient } from "wax-auth-client";
import AnchorLink from "anchor-link";
import AnchorLinkBrowserTransport from "anchor-link-browser-transport";

const auth = new WaxAuthClient();
let nonce;
let waxAddress;

const wax = new waxjs.WaxJS({
  rpcEndpoint: "https://api.wax.bountyblok.io",
});

function App() {
  const dapp = "wax-demo";
  const [show, setShow] = useState(false);

  const [showLink, setShowLink] = useState(false);

  const [anchor, setAnchor] = useState(false);

  const transport = new AnchorLinkBrowserTransport();
  const link = new AnchorLink({
    transport,
    chains: [
      {
        chainId:
          "1064487b3cd1a897ce03ae5b6a865651747e2e152090f99c1d19d44e01aea5a4",
        nodeUrl: "https://wax.greymass.com",
      },
    ],
  });

  const waxClick = async () => {
    waxAddress = await wax.login();
    console.log(`got wax wallet`, waxAddress);
    getNonce();
    setAnchor(false);
  };

  const anchorClick = async () => {
    const identity = await link.login(dapp);
    const { session } = identity;
    const key = String(session.auth);
    waxAddress = String(key.split("@")[0]);
    const type = String(key).split("@")[1];
    console.log(`got Anchor wallet ${waxAddress}`);
    setAnchor(true);
    getNonce();
  };

  const getNonce = async () => {
    let response = await fetch(`api/getNonce`, {
      method: "POST",
      credentials: "include",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        waxAddress,
      }),
    });

    nonce = (await response.json()).nonce;

    console.log(`nonce::`, nonce);

    setShow(true);
  };

  const verify = async () => {
    console.log(`i'm in here`, nonce);
    if (!nonce) return;

    const tx = auth.getTransactionData(nonce, waxAddress);

    const dataObj = tx.data.actions[0].data;

    const actionsObj = {
      account: tx.data.actions[0].account,
      name: tx.data.actions[0].name,
      authorization: tx.data.actions[0].authorization,
      data: dataObj,
    };

    try {
      let result;
      let proof;
      if (anchor === true) {
        const session = await link.restoreSession(dapp);
        result = await session.transact(
          {
            actions: [actionsObj],
          },
          tx.options
        );
        proof = {
          serializedTransaction: result.resolved.serializedTransaction,
          signatures: result.signatures,
        };
        setAnchor(false);
      } else {
        result = await wax.api.transact(
          {
            actions: [actionsObj],
          },
          tx.options
        );
        proof = {
          serializedTransaction: result.serializedTransaction,
          signatures: result.signatures,
        };
      }

      let res = await fetch(`api/verify`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          proof,
          nonce,
          waxAddress,
        }),
      });

      let main = await res.json();

      authenticate(main, () => {});

      console.log(`main`, main);

      setShowLink(true);
    } catch (error) {
      console.log(`Error:::`, error);
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <p>hello</p>
        {show ? (
          <></>
        ) : (
          <>
            <button onClick={waxClick}>Wax Login</button>
            <br />
            <button onClick={anchorClick}>Anchor Login</button>
          </>
        )}
        <br />
        {show && (
          <>
            <button onClick={verify}>Verifiy</button> <br />{" "}
            {showLink && (
              <Link to="/user" style={{ color: "white" }}>
                Go to user
              </Link>
            )}
          </>
        )}
      </header>
    </div>
  );
}

export default App;
