export const isAutheticated = () => {
  if (typeof window == "undefined") {
    return false;
  }
  if (localStorage.getItem("wax-demo-token")) {
    return JSON.parse(localStorage.getItem("wax-demo-token"));
  } else {
    return false;
  }
};

export const authenticate = (data, next) => {
  if (typeof window !== "undefined") {
    localStorage.setItem("wax-demo-token", JSON.stringify(data));
    next();
  }
};
